.mybtn {
  background-color: rebeccapurple !important;
  
  &:hover {
    background-color: rgba(88, 33, 143, 0.782) !important;
  }

  &:focus,
  &:focus-visible {
    outline: none;
  }
}

.mybtn.btn-disabled {
  background-color: rgb(94, 94, 94) !important;
  color: rgb(255, 255, 255) !important;
  // &:hover {
  //   background-color: rgba(88, 33, 143, 0.782) !important;
  // }

  &:focus,
  &:focus-visible {
    outline: none;
  }
}

.pointer {
  cursor: pointer;
}

.container {
  min-width: fit-content;
}

.login-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  // box-shadow: 3px 3px 15px rgba(162, 162, 162, 0.759);
  border: 2px solid rgba(102, 51, 153, 0.699);
  border-radius: 10px;
  margin: 2rem;
  padding: 2rem;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  width: 95dvw;
  max-width: 600px;

  form {
    display: flex;
    align-items: stretch;
    justify-content: center;
    flex-direction: column;
    width: 100%;

    label {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 1rem 0rem;
      // width: 70%;

      input {
        border-radius: 5px;
        padding: 5px;
        border: rebeccapurple 2px solid;
        width: 340px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
      }
    }
  }
}